import React, { Component } from 'react';
import './Head.css';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {BottomNavigation, BottomNavigationItem} from 'material-ui/BottomNavigation';
import ReactDOM from 'react-dom';
import axios from 'axios';
import RaisedButton from 'material-ui/RaisedButton';

import Home from 'material-ui/svg-icons/content/flag';
import Map from 'material-ui/svg-icons/maps/map';
import Graduation from 'material-ui/svg-icons/social/school';
import Check from 'material-ui/svg-icons/av/featured-play-list';
import defalt from './defalt.jpg';

import HomeItem from './Home/Home.js';
import MapItem from './Map/MapComponents/Map.js';
import GraduationItem from './Graduation/Graduation.js';
import CreditItem from './Credit/Credit.js';

import FadeIn from 'react-fade-in';

const homeIcon = <Home />;
const mapIcon = <Map />;
const graduationIcon = <Graduation />;
const checkIcon = <Check />;

let MapCourseData;
let StudentCosPas;
let flag = 1;

const studentCos = [{"cos_cname":"()","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"()","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"()","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"()","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"()","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"() ","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"1","semester":"2","suggest":"","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":null,"pre":null},{"cos_cname":"","grade":"1","semester":"2","suggest":"","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"()","pre":null},{"cos_cname":"","grade":"1","semester":"2","suggest":null,"pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"()","pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"","pre":""},{"cos_cname":"()","grade":"2","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"2","suggest":null,"pre":""},{"cos_cname":"","grade":"2","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":"()"},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":null},{"cos_cname":"()","grade":"3","semester":"2","suggest":null,"pre":"()"},{"cos_cname":"()","grade":"4","semester":"1","suggest":null,"pre":"()"}]
const studentPas = [{"cos_cname":"()","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"()","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"","grade":"1","semester":"1","suggest":null,"pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"()","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"() ","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"1","semester":"2","suggest":"","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":null,"pre":null},{"cos_cname":"","grade":"1","semester":"2","suggest":"","pre":null},{"cos_cname":"()","grade":"1","semester":"2","suggest":"()","pre":null},{"cos_cname":"","grade":"1","semester":"2","suggest":null,"pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"()","pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"1","suggest":"","pre":""},{"cos_cname":"()","grade":"2","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"2","semester":"2","suggest":null,"pre":""},{"cos_cname":"","grade":"2","semester":"2","suggest":"","pre":null},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":null},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":"()"},{"cos_cname":"","grade":"3","semester":"1","suggest":"","pre":null},{"cos_cname":"()","grade":"3","semester":"2","suggest":null,"pre":"()"},{"cos_cname":"()","grade":"4","semester":"1","suggest":null,"pre":"()"}]


class Head extends Component {

    constructor(props) {
        super(props);
    }

    state = {
       selectedIndex: 0,
       styleButton: {
       fontFamily: 'Noto Sans CJK TC',
       background: '#EEEEEE',
       },
		studentIdcard:{
       		name:'',
			prog:'',
			grad:'',
		},
    };


    componentWillMount(){
    	let _this = this;

        axios.get('/students/profile').then(studentData => {
            _this.setState({
                studentIdcard: {
                    name: studentData.data[0].sname,
                    prog: studentData.data[0].program ,
                    grad: "" + studentData.data[0].grade,
                }
            })
        }).catch(err => {
            console.log(err);
        });
            MapCourseData = Object.keys(studentCos).map(function(key) {
                let user = studentCos[key];
                user.id = key;
                return user;
            });
        StudentCosPas = Object.keys(studentPas).map(function(key) {
            let user = studentPas[key];
            user.id = key;
            return user;
        });
        axios.get('/students/courseMap').then(studentData => {
                MapCourseData = Object.keys(studentData.data).map(function(key) {
                    let user = studentData.data[key];
                    user.id = key;
                    return user;
                });
        }).catch(err => {
            console.log(err);
        });
        axios.get('/students/coursePass').then(studentData => {
            // studentData.status HTTP response code (e.g., 200, 401)
            // studentData.data object parsed from HTTP response body
            // studentData.headers  HTTP presonse headers

            StudentCosPas = Object.keys(studentData.data).map(function(key) {
                let user = studentData.data[key];
                user.id = key;
                return user;
            });

        }).catch(err => {
            console.log(err);
        });


    }

    componentDidMount(){
    	this.select(0);
	}

  	select(index){
    	if(flag){
    		flag = 0;
            function delay() {
                return new Promise(function (resolve, reject) {
                    setTimeout(function () {
                        resolve('');
                    }, 500);
                });
            }

            delay().then(function (value) {
                ReactDOM.render(
					<font>
						<FadeIn>
							<HomeItem />
						</FadeIn>
					</font>,
                    document.getElementById('page'));
            }).catch(function (error) {
                console.log(error);
            });
		}
		else {
            if(index===0){
                ReactDOM.render(
					<font>
						<FadeIn>
							<HomeItem />
						</FadeIn>
					</font>,
                    document.getElementById('page'));
            }
            else if(index===1){
                ReactDOM.render(
					<div>
						<FadeIn>
							<MapItem studentPasdata={StudentCosPas} data={MapCourseData} studentId={this.state.studentIdcard.prog}
									 studentsGrad={this.state.studentIdcard.grad}/>
						</FadeIn>
					</div>,
                    document.getElementById('page'));
            }
            else if(index===2){
                ReactDOM.render(
					<FadeIn>
						<GraduationItem />
					</FadeIn>,
                    document.getElementById('page'));
            }
            else if(index===3){
                ReactDOM.render(
					<a>
						<FadeIn>
							<CreditItem />
						</FadeIn>
					</a>,
                    document.getElementById('page'));
            }
		}

  		this.setState({selectedIndex: index});
  	}
    
	render() {
	    return (
		    <div id="Head">
				<div id="ontopDiv">
					<div className="Head-header" >
							<div id="rectangle1"> </div>
							<div id="h1"></div>
							<div id="h2">NCTU Curriculum Assistant</div>
						<div id="adjust">
						<MuiThemeProvider zDepth={1}>
							<BottomNavigation selectedIndex={this.state.selectedIndex}>
							  <BottomNavigationItem
								label=""
								icon={homeIcon}
								style={this.state.styleButton}
								onTouchTap={() => this.select(0)}
							  />
							  <BottomNavigationItem
								label=""
								icon={mapIcon}
								style={this.state.styleButton}
								onTouchTap={() => this.select(1)}
							  />
							  <BottomNavigationItem
								label=""
								icon={graduationIcon}
								style={this.state.styleButton}
								onTouchTap={() => this.select(2)}
							  />
							  <BottomNavigationItem
								label=""
								icon={checkIcon}
								style={this.state.styleButton}
								onTouchTap={() => this.select(3)}
							  />
							</BottomNavigation>
						</MuiThemeProvider>
						</div>
						<div className="idcard">
							<div id="idcard-data">
								<div id="idcard-photo">
									<img src={defalt} width="44px" />
								</div>
								<div id="idcard-top">
                                    {this.state.studentIdcard.name}
								</div>
								<div id="idcard-buttom">
                                    {this.state.studentIdcard.prog}{this.state.studentIdcard.grad}
								</div>
							</div>
						</div>
						<div id="logout-button">
							<MuiThemeProvider>
								<RaisedButton style={{
                                    width: '13%',
                                    fontFamily: 'Noto Sans CJK TC',
                                }}  backgroundColor = "#DDDDDD" label="Logout" href="/logout"/>
							</MuiThemeProvider>
						</div>
					</div>
				</div>
				<div id="topRec">
				</div>

				<div id="page" > </div>
				<footer>Copyright @2017 NCTUCS </footer>
	  		</div>
	    );
	  }
}

export default Head;



